import { createStore } from 'vuex'

export default createStore({
  state: {
    userData: null,
    userCartlistSubtotal: null
  },
  mutations: {
    updateData(state, data){
      state.userData = data;
      let subTotal = 0;
      for (let item in state.userData.testID.cartList) {
        subTotal += state.userData.testID.cartList[item].price * 
        state.userData.testID.cartList[item].qty;
      }
      state.userCartlistSubtotal = subTotal;
    },
    deleteItem(state, pID){
      delete state.userData.testID.cartList[pID];
    }
  },
  actions: {
  },
  modules: {
  }
})
