<script>
import '../firebase'

import { getDatabase, ref, onValue } from "firebase/database";

export default {
  name: 'Header',
  components: {
  },
  data(){
    return {
      navMobileMenu: false,
      qtyTemp: '',
      isCartQtyFocus: '',
      // userData: [],
    }
  },
  methods: {
    test(){
      // console.log(this.$store.state.userData.testID)
      // this.$store.commit('deleteItem')
    },
    deleteCartItem(pID) {
      // cartList是物件
      // 被點擊的按鈕會傳來該商品的pID索引值
      // 使用delete指令，刪除物件使用索引值進行刪除
      // delete this.userData.cartList[`${pID}`]
      this.$store.commit('deleteItem', pID)
    },
    updateDataToVuex(data){
      this.$store.commit('updateData', data)
    },
  },
  computed: {
    subTotalSum(){
      let sum = 0;
      // 由於cartList是物件，所以item是key值
      // 做法是把cartList的所有key值內容的價格遍歷並加總
      for (let item in this.$store.state.userData.testID.cartList) {
        sum += this.$store.state.userData.testID.cartList[item].price * this.$store.state.userData.testID.cartList[item].qty
      }
      return sum.toFixed(2);
    },
  },
  created(){
    // 創建Header組件時，檢查vuex內userData是否有值
    // 如果沒值，就從firebaseRTDB抓取資料回來並更新到vuex

    if (!this.$store.state.userData) {
      const db = getDatabase();
      const dbRef = ref(db, '/');
      onValue(dbRef, (snapshot) => {
        const data = snapshot.val();
        this.updateDataToVuex(data);
      });
    }

  },
}
</script>

